import { Form, useForm } from "@miq/shared";
import React from "react";
import { initialState } from "..";

export default function SettingsView(props) {
    const form = useForm({ label: "", valid: true });

    console.log(form);

    return (
        <div>
            <div className="">
                <Form context={form} className="form">
                    <Form.TextAreaX name="label" placeholder="This a placeholder" />
                    <Form.CheckboxInput name="valid" required />
                    {`${form.values.valid ? "ok" : "not ok"}`}

                    <input type="submit" />
                </Form>
            </div>
        </div>
    );
}

export const settingsReducer = (state = initialState.settings, action = {}) => {
    const { type, payload = {} } = action;

    switch (type) {
        case "RESET_SETTINGS":
            return { ...state };

        case "SET_THEME":
            return { ...state, ...payload };

        default:
            return state;
    }
};
